.trendingCoin {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #0c1a32;
  width: 260px;
  height: 100px;
  margin: 3px;
  padding: 5px;
  border-radius: 3px;
}
.trendingCoin__image {
  position: absolute;
  top: 20px;
  left: 15px;
  height: 35px;
  width: 35px;
}
.trendingCoin__display-name {
  position: absolute;
  top: 15px;
  left: 65px;
  font-family: "IBM Plex Sans", sans-serif;
  text-align: left;
}
.trendingCoin__display-name > p {
  margin: 0;
}
.trendingCoin__symbol {
  color: #8daac6;
}
.trendingCoin__current-price {
  position: absolute;
  bottom: -5px;
  left: 15px;
}
.trendingCoin__market_cap_rank {
  color: #8daac6;
  position: absolute;
  top: 15px;
  right: 15px;

  font-size: 16px;
}
@media (max-width: 800px) {
  .trendingCoin {
    width: 220px;
    height: 90px;
  }
  .trendingCoin__display-name > p {
    font-size: 14px;
  }
  .trendingCoin__market_cap_rank {
    font-size: 14px;
  }
  .trendingCoin__current-price {
    font-size: 14px;
  }
}
